.scrollup {
	position: fixed;
	right: 2.5rem;
	bottom: -20%;
	background-color: var(--title-color);
	opacity: 0.8;
	padding: 0.4rem 0.7rem;
	border-radius: 4rem;
	z-index: var(--z-tooltip);
	transition: 0.4s;
}

.scrollup__icon {
	font-size: 1.5rem;
	color: var(--container-color);
}

.scrollup:hover {
	background-color: var(--title-color-dark);
}

/* Show Scroll */
.show-scroll {
	bottom: 3rem;
}
